<template>
  <div class="book">
    <h1>{{ msg }}</h1>
    <input v-model="kw" v-on:change="loadDouban"><span v-on:click="flushDouban" v-bind:class="{ hidden: books.length == 0 }"></span>
    <bookitem v-bind:items="books"></bookitem>
    <div>
      :<input v-model="regBook.title" placeholder=""><br>
      ISBN:<input v-model="regBook.isbn" placeholder="isbn"><br>
      :<input v-model="regBook.user"><br>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import bookitem from './books/bookitem'
var pdata = {
  msg: '',
  kw: "",
  books: [],
  regBook:{}
}
export default {
  name: 'Book',
  data () {
    return pdata
  },
  methods: {
    flushDouban:function(){
      this.kw = ""
      this.books = []
    },

    loadDouban: function(){
      if (pdata.kw == ""){return}
      axios.get('/api/v1/book/suggest?k='+pdata.kw).then(response => {
        pdata.books = response.data
      }).catch(err => {
          console.log(err)
      })
    }
  },
  components: {bookitem}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



