<<template>
  <div class="story">
      <h1>{{ msg }}</h1>
      <p v-on:click="listStories"></p>
    <div class="stories">
        <div class="row">
            <div class="col-sm-3 col-md-6" v-for="item in stories">
                <div class="thumbnail">
                    <div class="caption">
                        <h3>{{ item.title }}</h3>
                        <p>{{ item.author }}</p>
                        <div class="story-content">
                            <p  v-for="p in item.ps">{{ p }}</p>
                        </div>
                        <p>
                            <a v-on:click="readToMe(item.id)" class="btn btn-primary" role="button"></a>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<<script>
import axios from 'axios'
var pdata = {
    msg: "",
    pn: 0,
    stories: [
        {title:"",author:"", body:""}
    ],
    audio: new Audio()
}
export default {
 name:'Story',
 data (){
     return pdata
 },
 methods:{
     listStories:function () {
         axios.get('/api/v1/story/list?pn=2&p='+pdata.pn).then(response =>{
             pdata.stories = response.data
             for (var index = 0; index < pdata.stories.length; index++) {
                 var item = pdata.stories[index]
                 item.audio = '/api/v1/story/tell?id='+item.id
                 item.ps = item.body.split("\n")
             }
         }).catch(err => {
             console.log(err)
         })
         pdata.pn += 1
     },
    readToMe: function (id) {
      this.audio.src = '/api/v1/story/tell?id='+id 
      this.audio.play()
    }
 }
}
</script>

<<style>
.story-content {
    text-align: start;
}
</style>





