<<template>
  <div class="bookitem">
    <div class="row">
      <div class="col-sm-3 col-md-2" v-for="item in items">
        <div class="thumbnail">
          <img :src="item.pic" alt="...">
          <div class="caption">
            <h3>{{ item.title }}</h3>
              <p>{{ item.author_name }}</p>
              <p>
                <a :href="item.url" target="_blank" class="btn btn-primary" role="button"></a>
              </p>
          </div>
        </div>
      </div>
    </div> 
  </div>
</template>

<<script>
export default {
 name: "bookitem",
 props: ["items"]
}
</script>

<<style>
.bookitem .thumbnail {
    height: 272px;
}
</style>




