<<template>
  <div class="trad">
    <h1 v-on:click="loadTrans">{{ msg }}</h1>
    <div class="panel panel-default" id="trad-list">
        <div class="panel-heading">
            <p></p>
        </div>
        <ul class="list-group">
          <li class="list-group-item" v-for="item in items">
            <span class="trad-time">{{ item.setup_fmt }}</span>: {{ item.to }}  {{ item.from }} 
          </li>
        </ul>
    </div>
  </div>
</template>

<<script>
import axios from 'axios'
var pdata = {
  msg: "",
  douban: "",
  items: []
}
export default {
  name: 'Trad',
  data (){
    return pdata
  },
  methods :{
    loadTrans: function(){
        axios.get('/api/v1/trans/list')
        .then(function (response) {
          pdata.items = response.data
        })
        .catch(function (error) {
          console.log(error);
        })
    }
  }
}
</script>

<<style>
#trad-list{
    text-align: left;
    margin-left: 8%;
    margin-right: 8%;
}
span.trad-time {
    color: #1c15d4;
}
</style>



